.facts{
    box-sizing: border-box;
    position: relative;
    padding: 100px 60px 50px;

&-title{
    font-weight: normal;
    font-size: 35px;
    line-height: 41px;
    text-align: center;
    color: #ffffff;
    margin-bottom: 30px;
}

&-slide {

    &-desc{
        margin-top: 40px;
        font-weight: normal;
        font-size: 27px;
        line-height: 32px;
        text-align: center;
        color: #FFFFFF;
    }
    }

    & > .swiper-button-prev, .swiper-button-next{
       color: #B38A7F;
    }

    & > .swiper-button-prev{
        left: 10px;
    }

    & > .swiper-button-next{
        right: 10px;
     }
     & > .swiper-button-disabled { 
opacity: 0;
     }

     &-pagination {
         display: flex;
         justify-content: center;
         margin-top: 66px;
         
        &-bullet {
            margin: 0 20px;
            display: inline-block;
            width: 24px;
            height: 24px;
            border-radius: 50%;
            background-color: transparent;
           border: 2px solid #B38A7F;
           opacity: 1;
           transition: 0.3s background-color;
       }

       & > .swiper-pagination-bullet-active{
        background-color: #B38A7F;
       }
     }
}